  nodeExpand(event: any) {
    if (event.node && event.node.data && event.node.type !== 'lookup') {
      this.mastersService.getLookupTree(event.node.data).subscribe((nodes: any) => {
        event.node.children = (<any>nodes).data;
      });
    }
  }

